import React from 'react';
import getText from '../utils/getText.js';
import Link from './Link.jsx';
import { Menu,
  Dropdown, Accordion, List } from 'semantic-ui-react';

const procedures = [
  {
    text: 'procedures.nose',
    url: '/procedures/rhinoplasty/'
  },
  {
    text: 'procedures.breast',
    url: '/procedures/breast-surgery/'
  },
  {
    text: 'procedures.ear',
    url: '/procedures/ear-surgery/'
  },
  {
    text: 'procedures.wrinkle',
    url: '/procedures/wrinkles/'
  },
  {
    text: 'procedures.eye',
    url: '/procedures/eyelid-surgery/'
  },
  {
    text: 'procedures.sweat',
    url: '/procedures/hyperhidrosis-treatment/'
  },
  {
    text: 'procedures.prp',
    url: '/procedures/prp-treatment/'
  },
  {
    text: 'procedures.baldness',
    url: '/procedures/baldness-treatment/'
  },
  {
    text: 'procedures.skin',
    url: '/procedures/skin-treatment/'
  },
  {
    text: 'procedures.mezo',
    url: '/procedures/mesotherapy/'
  }
];

export default class SiteNav extends React.Component {
  getProceduresMenu() {
    const links = [];
    const menuItems = [];
    let menu;

    for (var i = 0; i < procedures.length; i++) {
      links.push(<Link to={ procedures[i].url }>
                 { getText(procedures[i].text) }
                 </Link>);
    }

    if (this.props.type === 'sidebar') {
      for (var i = 0; i < links.length; i++) {
        menuItems.push(<List.Item key={ i }>
                         { links[i] }
                       </List.Item>);
      }
      menu = <Accordion>
               <Accordion.Title className='link'>
                 { getText('menu.procedures') }
               </Accordion.Title>
               <Accordion.Content>
                 <List link onClick={ this.props.onClick }>
                   { menuItems }
                 </List>
               </Accordion.Content>
             </Accordion>;
    } else {
      for (var i = 0; i < links.length; i++) {
        menuItems.push(<Dropdown.Item key={ i }>
                         { links[i] }
                       </Dropdown.Item>);
      }
      menu = <Dropdown text={ getText('menu.procedures') } className='link item'>
               <Dropdown.Menu>
                 { menuItems }
               </Dropdown.Menu>
             </Dropdown>;
    }

    return menu;
  }

  render() {
    const menuProps = {
      link: true,
      horizontal: this.props.type !== 'sidebar'
    };

    return <List { ...menuProps }>
             <List.Item name='home' onClick={ this.props.onClick }>
               <Link to={ '/' }>
               { getText('menu.home') }
               </Link>
             </List.Item>
             <List.Item name='noseart' onClick={ this.props.onClick }>
               <Link to={ '/procedures/rhinoplasty/' }>
               { getText('menu.rhino') }
               </Link>
             </List.Item>
             <List.Item name='procedures'>
               { this.getProceduresMenu() }
             </List.Item>
             <List.Item name='blog' onClick={ this.props.onClick }>
               <Link to={ '/blog/' }>
               { getText('menu.blog') }
               </Link>
             </List.Item>
             <List.Item name='contact' onClick={ this.props.onClick }>
               <Link to={ '/contact/' }>
               { getText('menu.contact') }
               </Link>
             </List.Item>
           </List>;
  }
}



