import React from 'react';
import PropTypes from 'prop-types';
import Link from 'gatsby-link';
import Helmet from 'react-helmet';
import Button from '../components/Button.jsx';
import SiteNav from '../components/SiteNav.jsx';
import { rhythm } from '../utils/typography';
import styles from '../css/main.scss';
import Waypoint from 'react-waypoint';
import semanticUIStyles from 'semantic-ui-css/semantic.min.css';
import FontAwesome from 'react-fontawesome';
import { Header,
  Menu,
  Dropdown,
  Container, Grid, List, Sidebar, Visibility } from 'semantic-ui-react';
import getText from '../utils/getText.js';

export default class Template extends React.Component {
  static propTypes = {
    children: PropTypes.any
  }

  state = {
    isMenuVisible: false,
    isStickyHeaderVisible: false
  };

  toggleMenu = () => {
    this.setState({
      isMenuVisible: !this.state.isMenuVisible
    });
  };

  showStickyHeader = () => {
    console.log('show');
    this.setState({
      isStickyHeaderVisible: true
    });
  };

  hideStickyHeader = () => {
    console.log('hide');
    this.setState({
      isStickyHeaderVisible: false
    });
  };

  render() {
    const helmetProps = {
      title: `${getText('info.name')} - ${getText('info.title')}`,
      meta: [{
        name: "description",
        content: "Sample"
      }, {
        name: "keywords",
        content: "sample, something, homepage"
      }]
    };

    const stickyHeaderProps = {
      className: `sticky-header ${this.state.isStickyHeaderVisible ? 'visible': ''}`
    };

    return (
      <div id='site'>
        <Helmet {...helmetProps} />
        <Waypoint onLeave={ this.showStickyHeader } onEnter={this.hideStickyHeader} topOffset={-200} bottomOffset={-200}>
          <header>
            <Container>
              <div className='header-info'>
                <div className='brand'>
                  <h1>{ getText('info.name') }</h1>
                  <span dangerouslySetInnerHTML={ { __html: getText('header.title') } }></span>
                </div>
                <div className='phone'>
                  { getText('info.phone') }
                </div>
              </div>
              <div className='menu desktop'>
                <SiteNav type='menu' />
              </div>
              <div className='menu-toggle' onClick={ this.toggleMenu }>
                <span>MENU</span>
                <FontAwesome name='bars' />
              </div>
            </Container>
          </header>
        </Waypoint>
        <header { ...stickyHeaderProps }>
          <Container>
              <div className='header-info'>
                <div className='brand'>
                  <h1>{ getText('info.name') }</h1>
                </div>
              </div>
              <div className='menu desktop'>
                <SiteNav type='menu' />
              </div>
              <div className='menu-toggle' onClick={ this.toggleMenu }>
                <span>MENU</span>
                <FontAwesome name='bars' />
              </div>
            </Container>
        </header>
        <Sidebar className='menu mobile' animation='overlay' direction='right' visible={ this.state.isMenuVisible } width='wide'>
                <div className='header'>
                  <div className='close' onClick={ this.toggleMenu }>
                  </div>
                </div>
                <div className='menu-content'>
                  <SiteNav type='sidebar' onClick={ this.toggleMenu } />
                </div>
              </Sidebar>
        { this.props.children() }
        <footer>
          <Container>
            <div className='social-media'>
              <FontAwesome name='facebook-square' />
              <FontAwesome name='linkedin-square' />
              <FontAwesome name='instagram' />
            </div>
            <div className='contact'>
              <div>
                { getText('info.phone') }
              </div>
              <div>
                { getText('info.email') }
              </div>
              <div>
                { getText('info.address') }
              </div>
            </div>
            <div className='navigation'>
              <ul>
                <li>
                  { getText('menu.home') }
                </li>
                <li>
                  { getText('menu.rhino') }
                </li>
                <li>
                  { getText('menu.procedures') }
                </li>
                <li>
                  { getText('menu.blog') }
                </li>
                <li>
                  { getText('menu.contact') }
                </li>
              </ul>
            </div>
          </Container>
        </footer>
        <div id='copyright'>
          Copyright ©
          { ` ${new Date().getFullYear()}` }
        </div>
      </div>
    )
  }
}



